.container-main {
  max-width: 900px;
  margin: 0 auto;
  padding: 30px 0;
  font-family: Montserrat;
}

@media (max-width: 970px) {
  .container-main {
    width: 93%;
    margin: 0 auto;
    padding: 30px 0;
  }
}

.remove-style-link a:link {
  color: black;
  text-decoration: none;
}

.remove-style-link a:visited {
  color: black;
  text-decoration: none;
}

.remove-style-link a:hover {
  color: black;
  text-decoration: none;
}

.remove-style-link a:active {
  color: black;
  text-decoration: none;
}
